body {
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
}

.search-toolbar {
  height: 15%;
  background-color: #8a2be2;
}

.search-form {
  justify-content: center;
  padding: 10px;
  display: flex;
}

