  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
  outline: none;
  padding: 10px;
  font-size: 15px;
}

.search-form-button {
  float: left;
  width: 70px;
  color: #000;
  cursor: pointer;
  background: #c3c2c2;
  border: none;
  border-top-right-radius: 2px;
