  border-right: 1px solid #c3c2c2;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  justify-content: center;
  align-items: center;
  justify-items: center;
  margin-bottom: 20px;
  font-size: 14px;
